<template>
    <div>
        <table class="table table-hover table-dark rounded rounded-4 overflow-hidden haven-table">
            <thead>
                <tr>
                    <th scope="col" style="width: 25%;">
                        <span>{{ text.hero }}</span>
                    </th>
                    <th scope="col" colspan="2">
                        <span>{{ text.drop }}&nbsp;1</span>
                    </th>
                    <th scope="col" colspan="2">
                        <span>{{ text.drop }}&nbsp;2</span>
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in Characters" :key="item">
                    <!-- <th scope="row" class="align-middle">{{ item.name }}</th> -->
                    <th scope="row" class="align-middle">
                        <img :src="`img/UI_Card/${item.Imgs[0]}`" :class="{ 'w-50': isMobile === false }" alt="">
                        <br>
                        <span class="mt-2">{{ item.Name[language] }}</span>
                    </th>
                    <td class="align-middle">
                        <img :src="`img/EquipmentItem/${item.HavenReward0[0]}`" :class="{ 'w-50': isMobile === false }" 
                        :alt="item.HavenReward0[0]">
                    </td>
                    <td class="align-middle">{{ item.HavenReward0[1] }}%</td>
                    <td class="align-middle">
                        <a :href="`/equipment?equipSearch=${item.HavenReward1[0]}&lang=${['en', 'kr', 'jp', 'zh'][language]}`" v-if="item.HavenReward1[0].includes('Accessory') || item.HavenReward1[0].includes('Armor')">
                            <img :src="`img/EquipmentItem/${item.HavenReward1[0]}`" :class="{ 'w-50': isMobile === false }" 
                            :alt="item.HavenReward1[0]">
                        </a>
                        <img :src="`img/EquipmentItem/${item.HavenReward1[0]}`" :class="{ 'w-50': isMobile === false }" 
                        :alt="item.HavenReward1[0]" v-else>
                    </td>
                    <td class="align-middle">{{ item.HavenReward1[1] }}%</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import Characters from '@/assets/json/Characters.json'

export default {
    name: 'HavenReward',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())

        return {
            isMobile,
            text,
            language,
            Characters
        }
    }
}
</script>

<style scoped>
table {
    table-layout:fixed;
    text-align: center;
}
img {
    width: 100%;
}
@media screen and (max-width: 768px) {
    .haven-table>thead>tr>th {
        font-size: xx-small;
    }
}
</style>
